@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container-site {
  width: 280px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container-site {
    width: 708px;
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .container-site {
    width: 1220px;
    padding: 0;
  }
}
.list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.title {
  margin: 0;
  color: #000000;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
}
@media (min-width: 768px) {
  .title {
    font-size: 60px;
    line-height: 60px;
  }
}
.title-decoration {
  display: block;
  position: relative;
}
.title-decoration::before {
  content: "";
  position: absolute;
  height: 2px;
  width: 100vw;
  background-color: #68B738;
  left: 50%;
  top: 63%;
  transform: translate(-50%, -50%);
  z-index: 1;
}
@media (min-width: 1440px) {
  .title-decoration::before {
    top: 70%;
  }
}
.title-decoration span {
  display: inline-block;
  position: relative;
  z-index: 2;
  background-color: #FFFFFF;
  padding-right: 22px;
}
@media (min-width: 768px) {
  .title-decoration span {
    padding-right: 37px;
    padding-left: 37px;
  }
}
@media (min-width: 1440px) {
  .title-decoration span {
    padding-left: 0;
    padding-right: 35px;
  }
}
.title-decoration span::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 24px;
  background-color: #FFFFFF;
  top: 0;
  left: -24px;
}
@media (min-width: 768px) {
  .title-decoration span::before {
    display: none;
  }
}
@media (min-width: 1440px) {
  .title-decoration span::before {
    display: block;
    width: 35px;
    left: -35px;
  }
}
@media (max-width: 767px) {
  .title-decoration--smal::before {
    top: 35%;
  }
}
.title-decoration--smal span {
  width: 186px;
  padding-right: 15px;
}
.title-decoration--smal span::before {
  width: 15px;
  left: -15px;
}
@media (min-width: 768px) {
  .title-decoration--smal span {
    width: auto;
    padding-right: 42px;
    padding-left: 0;
  }
  .title-decoration--smal span::before {
    display: block;
    width: 42px;
    left: -42px;
  }
}
.title-decoration--medium span {
  padding-right: 22px;
}
.title-decoration--medium span::before {
  width: 22px;
  left: -22px;
}
@media (min-width: 768px) {
  .title-decoration--medium span {
    padding-right: 38px;
    padding-left: 0;
  }
  .title-decoration--medium span::before {
    display: block;
    width: 38px;
    left: -38px;
  }
}
.title-decoration--big span {
  padding-right: 24px;
}
.title-decoration--big span::before {
  width: 24px;
  left: -24px;
}
@media (min-width: 768px) {
  .title-decoration--big span {
    padding-right: 41px;
    padding-left: 0;
  }
  .title-decoration--big span::before {
    display: block;
    width: 41px;
    left: -41px;
  }
}
@media (min-width: 1440px) {
  .title-decoration--smal span,
  .title-decoration--medium span,
  .title-decoration--big span {
    padding-right: 40px;
  }
}
.title-decoration--smal span::before,
.title-decoration--medium span::before,
.title-decoration--big span::before {
  display: block;
  width: 40px;
  left: -40px;
}
